
.Card{
  width: 100%;
  height: 100%;
  padding: 0.2rem;

  
}
.heading{
  display: flex;
  flex-direction: row;
  justify-content: space-between;

}
.cardname{
  padding: 0.2rem 0.1rem;
}
.card-content{
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  gap: 5px;
  font-size: 13px;
  white-space: normal;
}
.cardMedia{
  max-width: 100%;
  max-height:100%;
  
}
.cardMedia>img{
  max-width: 100%;
  max-height: 100%;
  object-fit: cover;
  border-radius: 8px;
}
.single-card{
display: flex;
flex-direction: column;
height: 100%;
gap: 5px;
text-align:left;
padding: 0.1rem;
margin:0.1rem;
border-radius: 8px;
}
@media(max-width:520px){
 .cardMedia{
  width: 100%;
 }
.cardname{
  font-size: 10px;
}
.rating{
  font-size: 11px;
  align-items: center;
}
.rating>svg{
  font-size: 11px;
}
.price{
  font-size: 11px;
}
.heading>h4{
  font-size:  22px;
  white-space: nowrap;
}
.heading>button{
  font-size: 10px;
  white-space: nowrap;
}
.single-card{
  height: 14rem;
}
}
@media(min-width:375px) and(max-width:520px){
  .single-card{
 
  }
}
@media(min-width:521px) and(max-width:768px){
  .single-card{
    height: 22rem;
  }
}
@media(min-width:1300px) and (max-width:1800px){
  .cardname{
    font-size: 18px;
  }
  .rating{
    font-size: 16px;
  }
  .price{
    font-size: 16px;
  }
  .heading>h4{
    font-size:  26px;
  }
  .heading>p{
    font-size: 15px;
  }
}
@media(min-width:1800px) and (max-width:3000px){
  .cardname{
    font-size: 22px;
  }
  .rating{
    font-size: 16px;
  }
  .price{
    font-size: 16px;
  }
  .heading>h4{
    font-size:  28px;
  }
  .heading>p{
    font-size: 17px;
  }
}